.descriptionMain{display:flex;flex-direction:column;align-items:flex-start;width:100%;min-height:400px}.descriptionMain .title{margin-bottom:24px;margin-left:30px;margin-top:-26px}.descriptionMain .title .title::before{width:118px;bottom:-5px}.descriptionMain .descriptionHeader{margin-bottom:1.5rem;position:absolute;bottom:15px;left:30px;z-index:2;color:var(--color-heading)}.descriptionMain .imgBlock{display:flex;justify-content:center;position:relative;width:100%;height:400px}.descriptionMain .imgBlock::before{content:"";position:absolute;top:0;bottom:0;left:0;right:0;opacity:.4;background:linear-gradient(180deg, rgba(20, 16, 21, 0) 0%, rgb(17, 17, 18) 67.23%, rgb(18, 20, 22) 100%)}.descriptionMain .imgBlock img{width:100%;object-fit:cover}.descriptionMain .linkBack{display:flex;color:var(--color-paragraph);cursor:pointer;align-items:center;text-decoration:none;font-size:18px;font-weight:600;margin:0 35px;letter-spacing:1px;padding:0 0 16px 0;transition:transform .5s}@media(max-width: 500px){.descriptionMain .linkBack{padding:16px 0 16px 0}}.descriptionMain .linkBack:hover{color:var(--color-accent);transform:scale(1.2)}.descriptionMain .linkBack i{margin-right:15px}.descriptionMain .linkBack i:before{content:"←"}.descriptionMain .textInfoBlock{padding:10px;display:flex;width:90%;height:100%;margin:20px auto;font-size:17px;justify-content:center;color:var(--color-paragraph);position:relative}.descriptionMain .textInfoBlock:before{content:"";position:absolute;background:var(--color-accent);width:70%;height:2px;left:50%;bottom:0;transform:translate(-50%, 50%)}.descriptionMain .descriptionLink{display:flex;justify-content:end;width:90%;margin:0 0 15px 0}